import React from 'react';
import { makeStyles } from '@mui/styles';
import { Button, ButtonGroup, Dialog, DialogTitle } from '@mui/material';

const useStyles = makeStyles({
  root: {
    minWidth: '150px',
  },
});

const NoticeDeleteDialog = (props) => {
  const classes = useStyles();
  const { onDelete, onClose, value, open } = props;

  const handleDelete = (i, j) => () => {
    onDelete(i, j);
    onClose();
  };

  const handleClose = () => {
    onClose();
  };

  return (
    <Dialog
      onClose={handleClose}
      aria-labelledby="delete-dialog-title"
      open={open}
      className={classes.root}
    >
      <DialogTitle id="delete-dialog">
          ?
      </DialogTitle>
      <ButtonGroup
        orientation="vertical"
        color="primary"
        aria-label="vertical contained primary button group"
        variant="text"
      >
        <Button onClick={handleDelete(value)}></Button>
        <Button onClick={handleDelete(value, true)}> </Button>
        <Button onClick={onClose}></Button>
      </ButtonGroup>
    </Dialog>
  );
};

export default NoticeDeleteDialog;
